<template>
    <yd-layout class="sealForm">
        <div class="fuyan" v-if="fuyanData!=''">
            <div class="fuyan_title">附言：</div>
            <div class="fuyan_text">{{fuyan}}</div>
        </div>
        <div class="appName">
            <div class="fluidWidth">
                <div class="appName_left"><span>申请人：</span>{{applicantname}}</div>
                <div class="appName_right" v-text="phone"></div>
            </div>
        </div>
        <div class="fillPart">
            <div class="partLine appDepart">
                <div class="pl_right">申请部门</div>
                <div class="pl_left departName" v-text="firstorgname"></div>
            </div>
            <div class="partLine appDate">
                <div class="pl_right">申请时间</div>
                <div class="pl_left departName" v-text="formatDate(applydate, 'yyyy-MM-dd')"></div>
                <yd-datetime type="date" v-model="applydate" v-show="false"></yd-datetime>
            </div>
            <div class="csName">
                <div class="partLine sealName">
                    <div class="pl_right">申请公章名称</div>
                    <div class="pl_left nameSelect" v-show="false">请选择</div>
                </div>
                <div class="applyname gzName" v-text="applyoffsname" @click="sealNamePopup=true" v-show="step==2"></div>
                <div class="applyname gzName" v-text="applyoffsname" v-show="step!=2"></div>
            </div>
            <div class="qzFile">
                <div class="partLine sealFile">
                    <div class="pl_right">签章文件</div>
                    <div class="pl_left nameSelect" id="js_upload" v-show="false">上传文件</div>
                </div>
                <div class="applyname qzName" v-for="(item, index) in qzwjfiles" @click="queryText(index)" :key="index" v-text="item" v-show="qzwjfiles.length!=0"></div>
            </div>
            <div class="qzBasis" v-show="qzyjfiles.length!=0">
                <div class="partLine sealBasis">
                    <div class="pl_right">签章依据</div>
                    <div class="pl_left nameSelect" v-show="false">上传文件</div>
                    <div class="pl_left" v-show="qzyjfiles.length==0">没有附件</div>
                </div>
                <div class="applyname qzName" v-for="(item, index) in qzyjfiles" @click="queryText(index+qzwjLength)" :key="index" v-text="item" v-show="qzyjfiles.length!=0"></div>
            </div>
        </div>
        <div class="reason">
            <div class="reasonTitle">申请原因</div>
            <div class="sqReason" v-html="transferString(reason)"></div>
            <trsTextarea placeholder="请填写您的申请原因..." v-model="reason" v-show="false"></trsTextarea>
        </div>
        <div class="reason remark_bg" v-show="remarks!=''">
            <div class="reasonTitle">备注</div>
            <div class="sqReason" v-html="transferString(remarks)"></div>
            <trsTextarea placeholder="请填写您的备注..." v-show="false"></trsTextarea>
        </div>
        <div class="backSuggest" v-show="applyLength!=0||leaderLength!=0||acceptLength!=0||assistLength!=0">
            <div class="suggestTitle">
                <div class="swiper-container">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide" @click="sugNum=1;opinions=formData.leaderopinions" :class="{active:sugNum==1}" v-show="leaderLength!=0">公司领导意见</div>
                        <div class="swiper-slide" @click="sugNum=2;opinions=formData.acceptopinions" :class="{active:sugNum==2}" v-show="acceptLength!=0">受理部门意见</div>
                        <div class="swiper-slide" @click="sugNum=3;opinions=formData.assistopinions" :class="{active:sugNum==3}" v-show="assistLength!=0">审查部门意见</div>
                        <div class="swiper-slide" @click="sugNum=4;opinions=formData.applyopinions" :class="{active:sugNum==4}" v-show="applyLength!=0">申请部门意见</div>
                    </div>
                </div>
            </div>
            <div class="suggestText" v-for="(item, index) in opinions" :key="index">
                <div class="name_sug"><span v-show="item.userName1!=''">{{item.userName1}}：</span>{{item.neirong}}</div>
                <div class="depart_time">
                    <div class="dt_one" v-show="item.orgName!=''">{{item.orgName}}</div>
                    <div class="dt_two" :class="{'dt_two_no':item.orgName==''}">{{getDay(item.submitDate)}}</div>
                </div>
            </div>
        </div>
        <yd-popup v-model="show" position="center" width="5.79rem" height="auto" v-show="show==true">
            <div class="other otherSeal">其他意见</div>
            <yd-cell-group>
                <yd-cell-item>
                    <yd-textarea id="js_otherInput" slot="right" placeholder="请您输入意见..." v-model="opinion"></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <div class="otherBtn">
                <ul>
                    <li class="yes sealYes" id="js_determineBtn" @click="show=false;leaderApprove()">确定</li>
                    <li class="no sealNo" id="js_cancelBtn" @click="show=false">取消</li>
                </ul>
            </div>
        </yd-popup>
        <yd-popup v-model="sealNamePopup" position="bottom" height="4rem" v-show="sealNamePopup==true">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="sealNamePopup = false">取消</div>
                <div class="rightBtn" v-on:click="sealNamePopup = false;">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" :class="{active:statusRadio==index}" @click="statusRadio=index;applyoffsname=item.offsname;applyoffsid=item.offsid" v-for="(item, index) in sealArray" :key="index" v-text="item.offsname">
                    </li>
                </ul>
            </div>
        </yd-popup>
        <!-- 领导审批 -->
        <yd-tabbar slot="tabbar" v-show="step==1&&type==0&&sealNamePopup==false">
            <div class="agree" @click="opinion='同意';leaderApprove()">同意</div>
            <div class="disagree" @click="opinion='不同意';leaderApprove()">不同意</div>
            <div class="otherAgree" @click="show=true">其他意见</div>
        </yd-tabbar>
        <!-- 申请人 -->
        <yd-tabbar slot="tabbar" v-show="step==2&&type==0&&sealNamePopup==false">
            <div class="sendLeader" @click="$router.push('/chooseLeader/'+workId+'/'+step)">送领导审批</div>
            <div class="submit" @click="submit()">提交</div>
        </yd-tabbar>
        <!-- 秘书 -->
        <yd-tabbar slot="tabbar" v-show="step==3&&type==0&&sealNamePopup==false">
            <div class="sendLeader" @click="$router.push('/secretaryDo/'+workId)">发送</div>
            <div class="submit backApply" @click="returnAccept()">发送至受理方</div>
        </yd-tabbar>
        <!-- 受理环节 -->
        <yd-tabbar slot="tabbar" v-if="step==4&&type==0&&sealNamePopup==false">
            <div class="agree" @click="$router.push('/chooseLeader/'+workId+'/'+step)">送领导审批</div>
            <div class="back" @click="$router.push('/sealAgent/'+$route.params.workId)">发送</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
// import axios from 'axios';
export default {
    name: 'commonSeal',
    data() {
        return {
            date: '2018-05-31',
            sugNum: 1,
            formData: {},
            reason: '', // 申请原因
            opinions: [], // 表单审批意见数组
            show: false, // 显示输入其他意见
            doKey: '', // 判断下部操作的key
            approveType: '', // 送领导审批的approveType
            leaderIDs: '', // 送领导审批的leaderIDs
            opinion: '', // 领导审批意见
            applicantname: '',
            phone: '',
            firstorgname: '',
            applydate: '',
            applyoffsname: '',
            remarks: '',
            applyoffsid: '',
            queryTypeArr: [23, 24, 25],
            approver: [],
            appleaderId: '',
            appleaderName: '',
            sec2LeaderId: '',
            sec2LeaderName: '',
            acceptLeaderId: '',
            acceptLeaderName: '',
            fuyan: '', // 附言
            sealNamePopup: false,
            sealArray: [],
            statusRadio: 999,
            qzwjfiles: [],
            qzyjfiles: [],
            qzwjLength: 0,
            allShow: false,
            applyLength: -1,
            leaderLength: -1,
            acceptLength: -1,
            assistLength: -1,
            fuyanData: '',
            file_url: [], // 附件地址
            submitLeaderIDs: '', // 提交按钮时传入的leaderIDs
            type: this.$route.params.type, // 判断是已办还是代办
            step: this.$route.params.step, // 是哪个页面
            workId: this.$route.params.workId, // 环节id
            errorTime: 0
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('公章申请');
        if (vm.step == 2 || vm.step == 4) {
            vm.getApprover();
            vm.getSealList();
        }
        // vm.getLast();
        vm.queryForm();
        vm.errorTime = 0;
    },
    watch: {
        approver: function() {
            let vm = this;
            vm.setSealInfo({
                approver: vm.approver
            })
        },
        approveType: function() {
            let vm = this;
            vm.setSealInfo({
                approveType: vm.approveType
            })
        },
        applyoffsid: function() {
            let vm = this;
            vm.setSealInfo({
                applyoffsid: vm.applyoffsid
            })
        },
        applyoffsname: function() {
            let vm = this;
            vm.setSealInfo({
                applyoffsname: vm.applyoffsname
            })
        }
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getSealInfo', 'getHandleInfo'])
    },
    methods: {
        ...mapMutations([
            'setSealInfo',
            'setMishuInfo',
            'setHandleInfo'
        ]),
        // 查询表单接口
        queryForm() {
            let vm = this;
            if (vm.getSealInfo.jsonData != '' && vm.getSealInfo.jsonData.workItemID == vm.$route.params.workId) {
                if (vm.getSealInfo.fuyan != '' && vm.getSealInfo.fuyan != '#none') {
                    vm.fuyanData = 'block';
                    vm.fuyan = vm.getSealInfo.fuyan;
                }
                vm.formData = vm.getSealInfo.jsonData;
                vm.applyLength = vm.formData.applyopinions.length;
                vm.leaderLength = vm.formData.leaderopinions.length;
                vm.acceptLength = vm.formData.acceptopinions.length;
                vm.assistLength = vm.formData.assistopinions.length;
                vm.opinions = vm.formData.leaderopinions; // 申请部门意见
                if (vm.leaderLength == 0 && vm.acceptLength != 0) {
                    vm.opinions = vm.formData.acceptopinions;
                    vm.sugNum = 2;
                } else if (vm.leaderLength == 0 && vm.acceptLength == 0 && vm.assistLength != 0) {
                    vm.opinions = vm.formData.assistopinions;
                    vm.sugNum = 3;
                } else if (vm.leaderLength == 0 && vm.acceptLength == 0 && vm.assistLength == 0 && vm.applyLength != 0) {
                    vm.opinions = vm.formData.applyopinions;
                    vm.sugNum = 4;
                }
                vm.applicantname = vm.formData.applicantname;
                vm.phone = vm.formData.phone;
                vm.firstorgname = vm.formData.firstorgname;
                vm.applydate = vm.formatDate(vm.formData.applydate, 'yyyy-MM-dd');
                vm.reason = vm.formData.reason;
                vm.applyoffsname = vm.formData.applyoffsname;
                vm.remarks = vm.formData.remarks;
                vm.qzwjfiles = vm.formData.qzwjfiles;
                vm.qzwjLength = vm.formData.qzwjfiles.length;
                vm.qzyjfiles = vm.formData.qzyjfiles;
                vm.applyoffsid = vm.formData.applyoffsid;
                // step==1是审批页面 vm.step == 2是申请人页面
                if (vm.step == 1 || vm.step == 2) {
                    for (let key in vm.formData.nextBtn) {
                        if (key.indexOf('officialSeal_acceptApplication') != -1) {
                            vm.doKey = key;
                            vm.leaderIDs = vm.formData.nextBtn.officialSeal_acceptApplication[0];
                        }
                        if (key.indexOf('officialSeal_secretaryApproval') != -1) {
                            vm.doKey = key;
                            vm.leaderIDs = vm.formData.nextBtn.officialSeal_secretaryApproval[0];
                        }
                        if (key.indexOf('officialSeal_leaderApproval') != -1) {
                            vm.doKey = key;
                            vm.leaderIDs = vm.formData.nextBtn.officialSeal_leaderApproval[0];
                        }
                        if (key.indexOf('doApprove') != -1) {
                            vm.doKey = key;
                            vm.approveType = vm.formData.nextBtn.doApprove.type;
                            for (let keyId in vm.formData.nextBtn.doApprove) {
                                if (keyId != 'type') {
                                    vm.leaderIDs = keyId;
                                }
                            }
                        }
                    }
                }
                // step==3是秘书页面
                if (vm.step == 3) {
                    for (let key in vm.formData.nextBtn) {
                        if (key.indexOf('officialSeal_leaderApproval') != -1) {
                            vm.appleaderId = vm.formData.nextBtn.officialSeal_leaderApproval[0];
                            vm.appleaderName = vm.formData.nextBtn.officialSeal_leaderApproval[1];
                            vm.setMishuInfo({
                                leaderId: vm.appleaderId,
                                leaderName: vm.appleaderName
                            })
                        }
                        if (key.indexOf('officialSeal_secretaryApproval2') != -1) {
                            vm.sec2LeaderId = vm.formData.nextBtn.officialSeal_secretaryApproval2[0];
                            vm.sec2LeaderName = vm.formData.nextBtn.officialSeal_secretaryApproval2[1];
                            vm.setMishuInfo({
                                sec2LeaderId: vm.sec2LeaderId,
                                sec2LeaderName: vm.sec2LeaderName
                            })
                        }
                        if (key.indexOf('officialSeal_acceptApplication') != -1) {
                            vm.acceptLeaderId = vm.formData.nextBtn.officialSeal_acceptApplication[0];
                            vm.acceptLeaderName = vm.formData.nextBtn.officialSeal_acceptApplication[1];
                        }
                    }
                }
                if (vm.formData.CURRENTSTATE != 10 && vm.$route.params.type == 0) {
                    vm.$dialog.toast({
                        mes: '该流程已被处理',
                        timeout: 2000,
                        callback: () => {
                            setTimeout(function() {
                                if (vm.$route.params.type == 0) {
                                    vm.$router.push('/handle');
                                }
                            })
                        }
                    });
                    if (vm.$route.params.type == 0) {
                        return false;
                    }
                }
                vm.loadSwiper();
                // let qzfiles = [...vm.qzwjfiles, ...vm.qzyjfiles];
                vm.loadFile();
            } else {
                vm.$dialog.loading.open('加载中...');
                vm.getLast();
                var promise = new Promise(function(resolve, reject) {
                    vm.getM(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                        'type': 'officialSeal',
                        'workItemID': vm.workId,
                        'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
                    }, function(res) {
                        if (res.isSuccess == 'false') {
                            vm.errorTime++;
                            if (vm.errorTime < 3) {
                                vm.queryForm();
                            } else {
                                vm.$dialog.loading.close();
                                vm.$dialog.toast({
                                    mes: '表单未加载成功',
                                    timeout: 2000,
                                    callback: () => {
                                        vm.$router.push('/handle');
                                    }
                                });
                            }
                        } else if (res.isSuccess == 'true') {
                            vm.$dialog.loading.close();
                        }
                        if (res.jsonData.CURRENTSTATE != 10 && vm.$route.params.type == 0 && res.isSuccess == 'true') {
                            vm.$dialog.toast({
                                mes: '该流程已被处理',
                                timeout: 2000,
                                callback: () => {
                                    setTimeout(function() {
                                        if (vm.$route.params.type == 0) {
                                            vm.$router.push('/handle');
                                        }
                                    })
                                }
                            });
                            if (vm.$route.params.type == 0) {
                                return false;
                            }
                        }
                        console.log(res.jsonData.reason);
                        vm.formData = res.jsonData;
                        res.jsonData.workItemID = vm.$route.params.workId;
                        vm.setSealInfo({
                            jsonData: res.jsonData
                        });
                        vm.applyLength = res.jsonData.applyopinions.length;
                        vm.leaderLength = res.jsonData.leaderopinions.length;
                        vm.acceptLength = res.jsonData.acceptopinions.length;
                        vm.assistLength = res.jsonData.assistopinions.length;
                        vm.opinions = res.jsonData.leaderopinions; // 申请部门意见
                        if (vm.leaderLength == 0 && vm.acceptLength != 0) {
                            vm.opinions = res.jsonData.acceptopinions;
                            vm.sugNum = 2;
                        } else if (vm.leaderLength == 0 && vm.acceptLength == 0 && vm.assistLength != 0) {
                            vm.opinions = res.jsonData.assistopinions;
                            vm.sugNum = 3;
                        } else if (vm.leaderLength == 0 && vm.acceptLength == 0 && vm.assistLength == 0 && vm.applyLength != 0) {
                            vm.opinions = res.jsonData.applyopinions;
                            vm.sugNum = 4;
                        }
                        vm.applicantname = res.jsonData.applicantname;
                        vm.phone = res.jsonData.phone;
                        vm.firstorgname = res.jsonData.firstorgname;
                        vm.applydate = vm.formatDate(res.jsonData.applydate, 'yyyy-MM-dd');
                        vm.reason = res.jsonData.reason;
                        vm.applyoffsname = res.jsonData.applyoffsname;
                        vm.remarks = res.jsonData.remarks;
                        vm.qzwjfiles = res.jsonData.qzwjfiles;
                        vm.qzwjLength = res.jsonData.qzwjfiles.length;
                        vm.qzyjfiles = res.jsonData.qzyjfiles;
                        vm.applyoffsid = res.jsonData.applyoffsid;
                        // step==1是审批页面 vm.step == 2是申请人页面
                        if (vm.step == 1 || vm.step == 2 || vm.step == 4) {
                            for (let key in vm.formData.nextBtn) {
                                if (key.indexOf('officialSeal_acceptApplication') != -1) {
                                    vm.doKey = key;
                                    vm.leaderIDs = vm.formData.nextBtn.officialSeal_acceptApplication[0];
                                }
                                if (key.indexOf('officialSeal_secretaryApproval') != -1) {
                                    vm.doKey = key;
                                    vm.leaderIDs = vm.formData.nextBtn.officialSeal_secretaryApproval[0];
                                }
                                if (key.indexOf('officialSeal_leaderApproval') != -1) {
                                    vm.doKey = key;
                                    vm.leaderIDs = vm.formData.nextBtn.officialSeal_leaderApproval[0];
                                }
                                if (key.indexOf('doApprove') != -1) {
                                    vm.doKey = key;
                                    vm.approveType = vm.formData.nextBtn.doApprove.type;
                                    for (let keyId in vm.formData.nextBtn.doApprove) {
                                        if (keyId != 'type') {
                                            vm.leaderIDs = keyId;
                                        }
                                    }
                                }
                            }
                        }
                        // step==3是秘书页面
                        if (vm.step == 3) {
                            for (let key in vm.formData.nextBtn) {
                                if (key.indexOf('officialSeal_leaderApproval') != -1) {
                                    vm.appleaderId = vm.formData.nextBtn.officialSeal_leaderApproval[0];
                                    vm.appleaderName = vm.formData.nextBtn.officialSeal_leaderApproval[1];
                                    vm.setMishuInfo({
                                        leaderId: vm.appleaderId,
                                        leaderName: vm.appleaderName
                                    })
                                }
                                if (key.indexOf('officialSeal_secretaryApproval2') != -1) {
                                    vm.sec2LeaderId = vm.formData.nextBtn.officialSeal_secretaryApproval2[0];
                                    vm.sec2LeaderName = vm.formData.nextBtn.officialSeal_secretaryApproval2[1];
                                    vm.setMishuInfo({
                                        sec2LeaderId: vm.sec2LeaderId,
                                        sec2LeaderName: vm.sec2LeaderName
                                    })
                                }
                                if (key.indexOf('officialSeal_acceptApplication') != -1) {
                                    vm.acceptLeaderId = vm.formData.nextBtn.officialSeal_acceptApplication[0];
                                    vm.acceptLeaderName = vm.formData.nextBtn.officialSeal_acceptApplication[1];
                                }
                            }
                        }
                        resolve(true);
                    }, function(res) {
                        if (res.response.status != 200) {
                            vm.errorTime++;
                            if (vm.errorTime < 3) {
                                vm.queryForm();
                                console.log(vm.errorTime);
                            } else {
                                vm.$dialog.loading.close();
                                vm.$dialog.toast({
                                    mes: '表单未加载成功',
                                    timeout: 2000,
                                    callback: () => {
                                        vm.$router.push('/handle');
                                    }
                                });
                            }
                        }
                    });
                });
                promise.then(function(data) {
                    if (data) {
                        vm.loadSwiper();
                        vm.loadFile();
                        vm.allShow = true;
                    }
                })
            }
        },
        // 获取审批人
        getApprover() {
            var vm = this;
            if (vm.getSealInfo.approver.length == 0) {
                for (var i = 0; i < vm.queryTypeArr.length; i++) {
                    var item = vm.queryTypeArr[i];
                    vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                        'type': 'officialSeal',
                        'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                        'queryType': item
                    }, function(res) {
                        vm.approver = [...vm.approver, ...res.jsonData];
                        if (item == 25) {
                            var hash = {};
                            const newArr = vm.approver.reduceRight((item, next) => {
                                hash[next.operatorid] ? '' : hash[next.operatorid] = true && item.push(next);
                                return item
                            }, []);
                            vm.approver = newArr;
                        }
                    }, function(res) {}, false);
                }
            }
        },
        // swiper插件
        loadSwiper() {
            let vm = this;
            vm.$nextTick(() => {
                this.galleryTop = new Swiper('.swiper-container', {
                    initialSlide: 0,
                    slidesPerView: 'auto',
                    paginationClickable: true,
                    observer: true, // 修改swiper自己或子元素时，自动初始化swiper
                    observeParents: true // 修改swiper的父元素时，自动初始化swiper
                })
            });
        },
        // upFile() {
        //     var file = document.getElementById('js_file');
        //     file.click();
        // },
        // 上一则附言
        getLast() {
            let vm = this;
            console.log(vm.getSealInfo.fuyan != '');
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/oapreviousfuyan', {
                'type': 'officialSeal',
                'workItemID': vm.workId,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                vm.fuyanData = res.jsonData;
                if (res.jsonData != '') {
                    vm.fuyan = res.jsonData[0].neirong;
                    vm.setSealInfo({
                        fuyan: res.jsonData[0].neirong
                    });
                } else {
                    vm.setSealInfo({
                        fuyan: '#none'
                    });
                }
            }, function(res) {}, false);
            // if (vm.getSealInfo.fuyan != '' && vm.getSealInfo.fuyan != '#none') {
            //     vm.fuyanData = 'block';
            //     vm.fuyan = vm.getSealInfo.fuyan;
            // } else if (vm.getSealInfo.fuyan == '') {
            //     vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/oapreviousfuyan', {
            //         'type': 'officialSeal',
            //         'workItemID': vm.workId,
            //         'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            //     }, function(res) {
            //         vm.fuyanData = res.jsonData;
            //         if (res.jsonData != '') {
            //             vm.fuyan = res.jsonData[0].neirong;
            //             vm.setSealInfo({
            //                 fuyan: res.jsonData[0].neirong
            //             });
            //         } else {
            //             vm.setSealInfo({
            //                 fuyan: '#none'
            //             });
            //         }
            //     });
            // }
        },
        // 获取签章列表
        getSealList() {
            let vm = this;
            if (vm.getSealInfo.sealArray.length == 0) {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                    'type': 'officialSeal',
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'queryType': 'gzmc'
                }, function(res) {
                    vm.sealArray = res.jsonData;
                    vm.setSealInfo({
                        sealArray: res.jsonData
                    })
                    console.log(res);
                })
            } else {
                vm.sealArray = vm.getSealInfo.sealArray;
            }
        },
        getDay(date) {
            date = date.replace(/-/g, '/');
            return (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '日';
        },
        // 领导审批按钮
        leaderApprove() {
            let vm = this;
            console.log(vm.doKey)
            if (vm.doKey == 'doApprove') {
                console.log(1)
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'officialSeal',
                    'approveType': vm.approveType,
                    'workItemID': vm.workId,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.formData.firstorgname + vm.formData.applicantname + '公章使用申请',
                    'ext': ''
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '审批完成',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            } else {
                console.log(2);
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'officialSeal',
                    'workItemID': vm.workId,
                    'activityID': vm.doKey,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.formData.firstorgname + vm.formData.applicantname + '公章使用申请',
                    'ext': ''
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '审批完成',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            }
        },
        // 提交按钮
        submit() {
            let vm = this;
            console.log(vm.leaderIDs);
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                'type': 'officialSeal',
                'workItemID': vm.workId,
                'activityID': 'officialSeal_acceptApplication',
                'leaderIDs': vm.leaderIDs,
                'fuyan': '',
                'opinion': '',
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'workTitle': vm.formData.firstorgname + vm.formData.applicantname + '公章使用申请',
                'ext': '{"applyid":"' + vm.getUserInfo.userid + '","applyname":"' + vm.applicantname + '","applyoffsid":"' + vm.applyoffsid + '","applyoffsname":"' + vm.applyoffsname + '","applyreason":"' + vm.transferString(vm.reason) + '","applyremark":"' + vm.transferString(vm.remarks) + '"}'
            }, function(res) {
                vm.$dialog.toast({
                    mes: '提交完成',
                    timeout: 2000,
                    callback: () => {
                        vm.$router.push('/handle/');
                    }
                });
            });
        },
        // 退回受理方
        returnAccept() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                'type': 'officialSeal',
                'workItemID': vm.workId,
                'activityID': 'officialSeal_acceptApplication',
                'leaderIDs': vm.acceptLeaderId,
                'fuyan': '',
                'opinion': '',
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'workTitle': vm.formData.firstorgname + vm.formData.applicantname + '公章使用申请',
                'ext': ''
            }, function(res) {
                vm.$dialog.toast({
                    mes: '审批完成',
                    timeout: 2000,
                    callback: () => {
                        vm.$router.push('/handle/');
                    }
                });
            })
        },
        loadFile() {
            let vm = this;
            let qzfiles = [...vm.qzwjfiles, ...vm.qzyjfiles];
            qzfiles.forEach(function(item, index) {
                if (item.indexOf('.rar') != -1) {
                    vm.file_url[index] = '.rar';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else if (item.indexOf('.zip') != -1) {
                    vm.file_url[index] = '.zip';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else {
                    let item2 = item.replace(/ /g, '#1mk');
                    vm.$http.get('/portalMgmt/oa/menus/getPdf', {
                        params: {
                            'type': 'officialSeal',
                            'fileName': item2,
                            'workTitle': vm.formData.firstorgname + vm.formData.applicantname + '公章使用申请',
                            'workItemID': vm.workId,
                            'resultType': 'queryAttachByWorkItemIDResult2',
                            'random': Math.floor(Math.random() * (100000 + 1))
                        }
                    }).then((res) => {
                        vm.file_url[index] = res.data.jsonData;
                        vm.setHandleInfo({ file_url: vm.file_url });
                    }).catch(error => {
                        // vm.$dialog.toast({
                        //     mes: '系统正在开小差，请稍后再试',
                        //     timeout: 1500
                        // });
                        console.log(error);
                    });
                }
            });
            // if (vm.getHandleInfo.file_url.length != qzfiles.length) {
            //     console.log(123);
            // }
        },
        queryText(index) {
            var vm = this;
            let detailUrl = vm.getHandleInfo.file_url[index];
            // console.log(detailUrl);
            let fileLength = [...vm.qzwjfiles, ...vm.qzyjfiles];
            console.log(fileLength.length)
            if (!detailUrl) {
                vm.$dialog.toast({
                    mes: '附件较大，正在加载中，请稍候',
                    timeout: 1500
                });
            } else if (detailUrl == '.rar') {
                vm.$dialog.toast({
                    mes: '暂不支持rar等压缩文件在线阅读  ',
                    timeout: 2000
                });
            } else if (detailUrl == '.zip') {
                vm.$dialog.toast({
                    mes: '暂不支持zip等压缩文件在线阅读  ',
                    timeout: 2000
                });
            } else {
                vm.page_jump(detailUrl);
            }
        },
        page_jump(detailUrl, isImg = 'notImg') {
            const vm = this;
            if (detailUrl.fileType != '.pdf') {
                let index1 = detailUrl.pdfUrl.lastIndexOf('.') + 1;
                let index2 = detailUrl.pdfUrl.length;
                let txtName = detailUrl.pdfUrl.substring(index1, index2);
                if (txtName != 'pdf') {
                    window.location.href = detailUrl.pdfUrl;
                } else {
                    var urls = vm.isIOS == true ? (detailUrl.pdfUrl.substring(0, detailUrl.pdfUrl.lastIndexOf('.')) + detailUrl.fileType) : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                    window.location.href = urls;
                }
                // var urls = vm.isIOS == true ? (detailUrl.pdfUrl.substring(0, detailUrl.pdfUrl.lastIndexOf('.')) + detailUrl.fileType) : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                // window.location.href = urls;
            } else {
                var urls2 = vm.isIOS == true ? detailUrl.pdfUrl : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                window.location.href = urls2;
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import "./commonSeal.less";

.dt_two_no {
    margin-left: 0 !important;
}

.gzName {
    color: #e33939 !important;
}

.gzName2 {
    color: #59a5fd !important;
}

.otherSeal {
    font-size: 0.32rem;
    font-weight: bold;
}

.yd-cell-box {
    margin-bottom: 0rem;
}

.sealYes {
    background-color: #59a5fd !important;
    height: 0.6rem !important;
    line-height: 0.6rem !important;
}

.sealNo {
    height: 0.6rem !important;
    line-height: 0.6rem !important;
}

.csName {
    padding-bottom: 0.2rem;
}

.otherBtn {
    overflow: hidden;
}
</style>


